/* Scss Document */
$font1: 'Averta';
$font2: 'Averta',
;
$color1: #F37021;
$color_border: #E5E5E5;
$color_hover: #004f8b;
$vne_color: #9F224E;
$url: "images" !default;

/*background: url("#{$url}/graphics/demo.gif") no-repeat 0 0;*/
@mixin cubic-transition($delay, $duration, $property) {
  transition: {
    duration: $duration;
    property: $property;
    timing-function: cubic-bezier(.7, 1, .7, 1);
  }
}

/* @include cubic-transition($delay: 0, $duration: 200ms, $property: (all)); */

@function px_to_rem($target, $context: $base-size) {
  @if $target==0 {
    @return 0
  }

  @return $target / $context+0rem;
}

$base-size: 16px;
$lh: 1.313;

@function calculateRem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}

@mixin fontSize($size) {
  font-size: calculateRem($size);
}

/**Apply: "font-size: px_to_rem(16px)" or "font: 400 px_to_rem(16px) arial;" **/

svg.icon,
.icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.icon {
  &.goal {
    color: #FFD30F;
  }
}

.hcvang {
  color: #FFD30F;
}

.hcbac {
  color: #BDBDBD;
}

.hcdong {
  color: #D65A1B;
}

%clearfix {
  clear: both;

  &:before,
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

%wcm {
  float: left;
  width: 100%;
}

%flex {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

%bgsvg {
  background-repeat: no-repeat;
  background-size: cover;
}

%flex-wrap {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}